@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap'); /*Dancing Script*/

.banner{
    font-family: 'Verdana';
    font-size: 1em;
    text-transform: uppercase;
    height: 100vh;
    padding: 1em 0;
    margin: auto;
    text-align: center;
}
.banner h4{
    padding: 1em;
    text-transform: capitalize;
    font-family: 'Dancing Script';
    font-size: 2em;
}